import React, { useEffect, useState } from "react";

import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import FormHelperText from "@mui/material/FormHelperText";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";

import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import OtpInput from "react-otp-input";
import { useLocation, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import { get, isEmpty } from "lodash";
import { useDispatch, useSelector } from "react-redux";
import {
  getData,
  handleOtpCode,
  postForgetPasswordOtpVerify,
} from "../../../store/slice/auth/signin/forgetPasswordSlice";
import ComponentStyle from "../../../styles/ComponentStyle.module.css";
import CMLoadingButton from "../../ui-components/buttons/CMLoadingButton";
import { postResendOtp } from "../../../store/slice/auth/signup/otpVerificationSlice";
import translate from "../../hooks/translate";

const ForgetPasswordOtp = () => {
  const [otpCode, setOtpCode] = useState("");
  const [erorrAlert, setErorrAlert] = useState(false);

  const { executeRecaptcha } = useGoogleReCaptcha();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const loading = useSelector((state) => state.forgetPassword.loading);
  const location = useLocation();
  const { state } = location;

  const store = useSelector(getData);
  const userData = get(store, "data", []);

  const handleChange = (code) => {
    setOtpCode(code);
  };

  useEffect(() => {
    if (
      get(state, "isPasswordVerified", null) === null ||
      isEmpty(get(userData, "email", "")) === true
    ) {
      navigate("/recover/account");
    }
  }, [navigate, state, userData]);

  const handleSubmitOtp = async () => {
    if (otpCode.length === 6) {
      setErorrAlert(false);
      if (!executeRecaptcha) {
        toast.error("You are not authorized");
        return;
      }
      const token = await executeRecaptcha("yourAction");

      const payload = {
        otp: otpCode,
        email: get(userData, "email", ""),
        googleRecaptchaToken: token,
      };
      const res = await dispatch(postForgetPasswordOtpVerify(payload))
        .unwrap()
        .then((result) => {
          if (result.status) {
            dispatch(handleOtpCode(otpCode));
            navigate("/forget-password-reset", {
              state: { isPasswordVerified: true },
            });
          } else {
            toast.error(result.message);
          }
        })
        .catch((error) => {
          console.log("error OTP verify", error);
        });
      return res;
    } else {
      setErorrAlert(true);
    }
  };

  const resendOtp = async () => {
    if (!executeRecaptcha) {
      toast.error("You are not Authorized");
      return;
    }
    const token = await executeRecaptcha();

    const payload = {
      email: get(userData, "email", ""),
      googleRecaptchaToken: token,
    };
    const res = await dispatch(postResendOtp(payload))
      .unwrap()
      .then((result) => {
        if (result.status) {
          toast.success(result.message, {
            position: toast.POSITION.TOP_CENTER,
          });
        }
      })
      .catch((error) => {
        console.log("OTP error=======>", error);
      });
    return res;
  };
  return (
    <Box component="div" sx={{ width: "100%" }}>
      <Container maxWidth="xl">
        <Grid container>
          <Grid
            item
            xs={12}
            sm={12}
            md={10}
            lg={8}
            sx={{ mx: { md: "0", xs: "auto" } }}
          >
            <Typography variant="h2" align="center" mb={3}>
              {translate("common_enter_verification_code")}
            </Typography>
            <Grid container spacing={2} justifyContent="center">
              <Grid item xs={12}>
                <OtpInput
                  value={otpCode}
                  onChange={handleChange}
                  numInputs={6}
                  separator={<span style={{ width: "8px" }}></span>}
                  isInputNum={true}
                  shouldAutoFocus={true}
                  className={ComponentStyle.otp_inputs}
                  containerStyle={{
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                  hasErrored={erorrAlert}
                  errorStyle={{
                    color: "#b71c1c",
                    borderColor: "#b71c1c",
                  }}
                  inputStyle={{
                    border: "1px solid #CFD3DB",
                    borderRadius: "8px",
                    width: "40px",
                    height: "40px",
                    fontSize: "16px",
                    color: "#777E89",
                    fontWeight: "400",
                    caretColor: "#777E89",
                    margin: "0 auto",
                  }}
                  focusStyle={{
                    border: "1px solid #CFD3DB",
                    outline: "none",
                  }}
                />
                {erorrAlert && (
                  <FormHelperText
                    error={true}
                    sx={{ textAlign: "center", mt: 1 }}
                  >
                    {translate("common_error_all_fields_are_required")}
                  </FormHelperText>
                )}
                <Typography variant="body1" align="center" mt={2}>
                  {translate("common_didn't_receive_code")} ?
                  <Typography
                    variant="body1"
                    color="primary"
                    component="span"
                    className="cursor-pointer"
                    ml={1}
                    onClick={resendOtp}
                  >
                    {translate("common_resend")}
                  </Typography>
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <CMLoadingButton
                  variant="contained"
                  fullWidth
                  type="submit"
                  loading={loading}
                  disabled={loading}
                  onClick={handleSubmitOtp}
                >
                  {translate("common_verify_otp")}
                </CMLoadingButton>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default ForgetPasswordOtp;
